import { BannerList } from "../list/BannerList";
import { useEffect, useRef } from "react";

var Image_Count = 0;
var theInterval;

export default function BannerCom() {
  const myTitle = useRef(null);
  const myhref = useRef(null);
  const myBanner = useRef(null);
  const mybannercon = useRef(null);

  function banner_aniamtion() {
    if (myBanner.current != null) {
      myBanner.current.src = BannerList[Image_Count].banner;
      myBanner.current.setAttribute("alt", BannerList[Image_Count].name);
      myBanner.current.setAttribute("title", BannerList[Image_Count].name);
      myTitle.current.innerHTML = BannerList[Image_Count].name;
      myhref.current.href = BannerList[Image_Count].href;

      if (Image_Count < BannerList.length - 1) {
        Image_Count++;
      } else {
        Image_Count = 0;
      }
      if (Image_Count == BannerList.length-1) {
        myhref.current.setAttribute("target", "_blank");
      } else {
        myhref.current.setAttribute("target", "");
      }
      theInterval = setTimeout(() => banner_aniamtion(), 4000);
    }
  }

  function stopSlide() {
    clearTimeout(theInterval);
  }
  function playSlide() {
    theInterval = setTimeout(() => banner_aniamtion(), 2000);
  }

  useEffect(() => {
    banner_aniamtion();
  }, []);

  return (
    <div
      onMouseOver={stopSlide}
      onMouseOut={playSlide}
      className="thumb thumb-banner"
    >
      <a
        ref={myhref}
        target={""}
        href={BannerList[0].href}
        className="banner_flex"
      >
        <img
          ref={myBanner}
          className="banner_image"
          alt={BannerList[0].name}
          src={BannerList[0].banner}
        ></img>
        <div ref={myTitle} className="banner_title">
          {BannerList[0].name}
        </div>
      </a>
      {}
    </div>
  );
}
